.pizza_input {
    height: 12%;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    @media (orientation: portrait) {
        height: 12%; }

    &--unos {
        margin-top: 20%;
        @media (max-width: 1400px) and (orientation: landscape) {
            margin-top: 18%; }
        @media (orientation: portrait) {
            margin-bottom: 22%; } }

    &--dos {
        margin-bottom: 20%;
        @media (max-width: 1400px) and (orientation: landscape) {
            margin-bottom: 22%; }
        @media (orientation: portrait) {
            margin-bottom: 18%; } }

    &__form {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        align-items: center; }

    &__input {
        text-align: center;
        flex-grow: 1;
        width: 20%;
        margin: 0.5vh 0;
        padding: 4px;
        transition: .3s;
        font-size: 2vh;
        color: #292929;
        border: 2px solid #e3e0e0;
        border-radius: 10px;
        outline: none;
        @media (orientation: portrait) {
            border-radius: 4px;
            font-size: 2vw;
            padding: 0;
            margin: 0.4vw 0;
            border-width: 1px; }

        &:focus {
            border-color: #3dbacc;
            border-radius: 14px;
            @media (orientation: portrait) {
                border-radius: 8px; } }

        &--valid {
            border-color: #46d190; }

        &--invalid {
            border-color: #E7655C; } } }
